@use "../../scss/abstracts";

.wp-block-md-anitian-fse-full-sample-block {
  background-color: abstracts.$green-theme-color;
  color: abstracts.$white-theme-color;
  padding: 5px 10px;
}

.md_bg_image_upload {
  width: 100%;
  height: 100%;
  border: 1px solid gray;
  justify-content: center;
  align-items: center;
}
.md_bg_image_upload img {
  height: 110px;
  width: 100%;
}
.md_bg_image_upload i {
  width: 100%;
  height: 100%;
  font-size: 100px;
}

.md_anitian_text_video_wrap {
  position: relative;
  .md_anitian_text_video {
      background-position: center !important;
      background-size: cover !important;
      background-blend-mode: normal;
      padding: 60px 0;
      .md_anitian_text_video__inner {
          display: flex;
          gap: 7%;
          align-items: center;
          .md_anitian_text_video__heading {
              max-width: 51%;
              width: 100%;
              h4 {
                  color: #0c71c3;
                  line-height: 1.4em;
                  letter-spacing: .55px;
                  margin: 0 0 10px;
                  font-weight: 700;
              }
              h2 {
                  font-weight: 600;
                  line-height: 1.4em;
                  margin: 0 0 20px;
                  color: #000;
                  font-size: 40px;
              }
              p {
                  color: #000;
                  margin-bottom: 1em;
              }
              .md_anitian_text_video__btn a {
                color: #fff;
              }
          }
          .md_anitian_text_video__youtube {
              max-width: 42%;
              width: 100%;
              .video_section_wrapper {
                  width: 100%;
                  margin: auto;
                  box-shadow: 6px 6px 18px 0px rgba(0, 0, 0, .3);
                  border-radius: 30px 0 30px 30px;
                  overflow: hidden;
                  background: #fff;
                  iframe {
                      width: 100%;
                      min-height: 300px;
                      height: 100%;
                  }
              }
          }
      }
  }
}
